@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic);
html {font-size: 16px}
/* body {font-family: 'Nanum Gothic'; background-color:#ccc} */
a {color: #333;}

input[type="text"], input[type="number"], input[type="password"] {
    margin: 0;
    padding: 0;
    border: 1px solid #ddd;
    color: inherit;
    font-family: inherit;
    outline: none;
    -webkit-appearance: none;
}

button {
    background-color: transparent;
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: top;
    outline: none;
    cursor: pointer;
    font-family: inherit;
}

